/* Base Styles with Modern Gradient */
.about-container {
  min-height: 100vh;
  background: linear-gradient(45deg, #0f2027, #203a43, #2c5364);
  color: #fff;
  position: relative;
}

/* Animated Background Particles */
.particles {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.particle {
  position: absolute;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  pointer-events: none;
  animation: particleFloat 20s infinite;
}

@keyframes particleFloat {
  0%, 100% {
    transform: translateY(0) translateX(0) rotate(0deg);
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    transform: translateY(-1000px) translateX(100px) rotate(360deg);
    opacity: 0;
  }
}

/* Neon Title Effect */
.glowing-text {
  font-size: 3.5rem;
  text-align: center;
  color: #fff;
  text-shadow: 0 0 10px #00ff00,
               0 0 20px #00ff00,
               0 0 30px #00ff00,
               0 0 40px #00ff00;
  animation: neonPulse 1.5s infinite alternate;
}

@keyframes neonPulse {
  from {
    text-shadow: 0 0 10px #00ff00,
                 0 0 20px #00ff00,
                 0 0 30px #00ff00,
                 0 0 40px #00ff00;
  }
  to {
    text-shadow: 0 0 5px #00ff00,
                 0 0 10px #00ff00,
                 0 0 15px #00ff00,
                 0 0 20px #00ff00;
  }
}

/* Section Cards with Glass Morphism */
section {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 2rem;
  margin: 2rem 0;
  transform-style: preserve-3d;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

section:hover {
  transform: translateY(-10px) rotateX(5deg);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
}

/* 3D Card Flip Animation */
.feature-card {
  perspective: 1000px;
  background: transparent;
}

.feature-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.feature-card:hover .feature-card-inner {
  transform: rotateY(180deg);
}

.feature-card-front, .feature-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  padding: 2rem;
}

.feature-card-back {
  transform: rotateY(180deg);
  background: rgba(255, 255, 255, 0.15);
}

/* Floating Stats Animation */
.stat-card {
  animation: floatStats 3s ease-in-out infinite;
  transform-style: preserve-3d;
}

@keyframes floatStats {
  0%, 100% {
    transform: translateY(0) rotateX(0);
  }
  50% {
    transform: translateY(-20px) rotateX(5deg);
  }
}

/* Progress Bar Animation */
.progress-bar {
  height: 4px;
  background: linear-gradient(90deg, #00ff00, #00ffff);
  width: 0%;
  position: fixed;
  top: 0;
  left: 0;
  animation: progressGrow 3s ease-out forwards;
}

@keyframes progressGrow {
  to {
    width: 100%;
  }
}

/* Ripple Effect on Click */
.ripple {
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  animation: ripple 0.6s linear;
  background-color: rgba(255, 255, 255, 0.7);
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}

/* Responsive Design with Animation */
@media (max-width: 768px) {
  section {
    transform: none !important;
    animation: mobileSlideIn 0.5s ease-out;
  }

  @keyframes mobileSlideIn {
    from {
      opacity: 0;
      transform: translateX(-100px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
}


/* Style for Return Button */
.return-button {
position: absolute;
top: 20px;
left: 20px;
padding: 10px 20px;
background-color: rgba(0, 255, 0, 0.7); /* Green background */
color: white;
font-size: 1.2rem;
border: none;
border-radius: 5px;
cursor: pointer;
z-index: 10;
transition: background-color 0.3s ease;
}

.return-button:hover {
background-color: rgba(0, 255, 0, 1); /* Darker green on hover */
}





